const failPic = '<img class="end-pic" src="./assets/img/failure.jpg" alt="fail pic">';
const winPic = '<img class="end-pic" src="./assets/img/success.jpg" alt="win pic">';
const resetBtn = '<button class="reset btn bg-danger">reset</button>';
const repeatWordsBtn = '<button class="repeat btn bg-warning">Repeat difficult words</button>';

function lossRate(wins, fails) {
  if (fails === 0 && wins === 0) return ' ';
  if (fails === 0) return '&#9989';
  const sum = wins + fails;
  const rate = (fails / sum) * 100;
  return Math.round(rate);
}

