import { clearContainer } from './playMode';

function sortUp(a, b) {
  return a.children[window.columnId].innerHTML - b.children[window.columnId].innerHTML;
}

function sortDown(a, b) {
  return b.children[window.columnId].innerHTML - a.children[window.columnId].innerHTML;
}

function sortWordsDown(a, b) {
  if (a.children[window.columnId].innerHTML > b.children[window.columnId].innerHTML) return 1;
  if (a.children[window.columnId].innerHTML < b.children[window.columnId].innerHTML) return -1;
}

function sortWordsUp(a, b) {
  if (a.children[window.columnId].innerHTML > b.children[window.columnId].innerHTML) return -1;
  if (a.children[window.columnId].innerHTML < b.children[window.columnId].innerHTML) return 1;
}


function sortTable(event) {
  let { target } = event;
  const arrTemp = [];
  window.columnId = event.target.getAttribute('data-table');
  if (window.columnId === null) return;


  while (target.tagName !== 'THEAD') {
    target = target.parentNode;
  }
  const container = target.nextElementSibling.nextElementSibling;
  const tableElems = target.nextElementSibling.nextElementSibling.children;
  for (const tr of tableElems) {
    arrTemp.push(tr);
  }
  clearContainer(container);
  if (event.target.classList.contains('up')) {
    if (window.columnId > 1) {
      arrTemp.sort(sortUp);
    } else {
      arrTemp.sort(sortWordsUp);
    }
  } else if (window.columnId > 1) {
    arrTemp.sort(sortDown);
  } else {
    arrTemp.sort(sortWordsDown);
  }
  arrTemp.forEach((tr) => {
    container.append(tr);
  });
  // console.log(arrTemp[0].children[0].innerHTML)
}

export default sortTable;
