import { deactivatedGame, clearContainer } from './playMode';
import { creatorStatisticsLayout, resetBtn, repeatWordsBtn } from './layout';
import sortTable from './sorting';
import { activationLink, renderCategory } from './render';
import { modificatedCards, cards } from './cards';


function repeatWords() {
  let maxPercent = 0;
  let idCard;
  let idCategory;
  let maxObj = {};
  const repeatWordsArr = [];
  const cardsData = JSON.parse(localStorage.getItem('stats'));
  for (let g = 0; g < 8; g += 1) {
    for (let i = 0; i < 8; i += 1) {
      for (let n = 0; n < cardsData[i].length; n += 1) {
        const currentPercent = cardsData[i][n].failClicks
        / (cardsData[i][n].failClicks + cardsData[i][n].winClicks);
        if (cardsData[i][n].failClicks === 0) continue;
        if (currentPercent > maxPercent) {
          maxPercent = currentPercent;
          maxObj = cardsData[i][n];
          idCard = n;
          idCategory = i;
        }
      }
    }
    if (maxPercent !== 0) {
      cardsData[idCategory].splice(idCard, 1);
    }
    if (maxObj.word !== undefined) repeatWordsArr.push(maxObj);
    maxPercent = 0;
    maxObj = {};
  }
  if (repeatWordsArr.length < 8) {
    alert('no words to repeat, play more');
    return;
  }
  const newCardsNinthCategory = JSON.parse(localStorage.getItem('stats'));
  newCardsNinthCategory[8] = repeatWordsArr;
  localStorage.setItem('stats', JSON.stringify(newCardsNinthCategory));
  window.play = 1;
  renderCategory(8);
  document.querySelector('.btn-change-mode').disabled = true;
}

export function renderStats() {
  const cardsData = JSON.parse(localStorage.getItem('stats'));
  const container = document.querySelector('.row');
  clearContainer(container);
  deactivatedGame();
  document.querySelector('.mode-container').classList.add('hidden');
  container.innerHTML = `${resetBtn + repeatWordsBtn}<br>`;
  for (let i = 0; i < 8; i += 1) {
    container.innerHTML += creatorStatisticsLayout(i, cardsData);
  }
  activationLink('stats');

  [...document.querySelectorAll('.table')].forEach((table) => {
    table.addEventListener('click', sortTable);
  });


  document.querySelector('.reset').addEventListener('click', resetStats);
  document.querySelector('.repeat').addEventListener('click', repeatWords);
}

function resetStats() {
  localStorage.setItem('stats', JSON.stringify(modificatedCards(cards)));
  renderStats();
}
export function writeStats(categoryId, wordId, prop) {
  const cardsData = JSON.parse(localStorage.getItem('stats'));
  cardsData[categoryId][wordId][prop] += 1;
  localStorage.setItem('stats', JSON.stringify(cardsData));
}
