import { renderMain, renderCategory } from './render';
import { renderStats } from './statistics';

function closeMenu(e) {
  if (!(e.target.classList.contains('tpl_hamburger'))) {
    document.querySelector('.navigation').classList.remove('active-nav');
    document.querySelector('#sandwichmenu').classList.remove('active-burger');
    document.querySelector('#sandwichmenu').classList.remove('active');
  }
  document.body.removeEventListener('click', closeMenu);
}
function bodyHandlerCloseMenu() {
  document.body.addEventListener('click', closeMenu);
}

function changeMenu() {
  document.querySelector('#sandwichmenu').addEventListener('click', function activationBurger() {
    this.classList.toggle('active');
    this.classList.toggle('active-burger');
    document.querySelector('.navigation').classList.toggle('active-nav');
    if (this.classList.contains('active-burger')) setTimeout(bodyHandlerCloseMenu, 50);
  });
}
changeMenu();

function menuNavigation() {
  document.querySelector('.category-menu').addEventListener('click', (e) => {
    const category = e.target.getAttribute('data-category');
    if (category === null || e.target.classList.contains('active')) return;
    if (category === 'main') {
      renderMain();
    } else if (category === 'stats') {
      renderStats();
    } else renderCategory(category);
  });
}
menuNavigation();
