import { clearContainer, deactivatedGame } from './playMode';
import { mainLayout, creatorCategoriesLayout } from './layout';
// import { cards } from './cards';
import { writeStats } from './statistics';

window.play = 0;
const container = document.querySelector('.row');
const btnChange = document.querySelector('.btn-change-mode');
const cards = JSON.parse(localStorage.getItem('stats'));

function rotate(event) {
  if (document.documentElement.clientWidth < 1000) return;
  const cardItem = this.querySelector('.card-img-top');
  const halfHeight = cardItem.offsetHeight / 2;
  const halfWidth = cardItem.offsetWidth / 2;
  cardItem.style.transform = `rotateX(${-(event.offsetY - halfHeight) / 5}deg) rotateY(${
    (event.offsetX - halfWidth) / 5}deg)`;
}

function rotateRemove() {
  if (document.documentElement.clientWidth < 1000) return;
  this.querySelector('.card-img-top').style.transform = 'rotateX(0deg) rotateY(0deg)';
}

function handlerRotate() {
  const cardItems = document.querySelectorAll('.card-item');
  cardItems.forEach((cardItem) => {
    cardItem.addEventListener('mousemove', rotate);
    cardItem.addEventListener('mouseout', rotateRemove);
  });
}


function hangsAnEvent(e) {
  const id = container.getAttribute('data-category');
  const index = this.id;
  function flipRemove() {
    this.classList.remove('rotate');
    setTimeout(() => {
      this.querySelector('.card-header').innerHTML = `${cards[id][index].word} <img class="rotate-button" src="./assets/img/rotate.svg" alt="rotate pic">`;
      this.querySelector('.card-header').classList.remove('reflection');
    }, 150);
    this.removeEventListener('mouseleave', flipRemove);
  }

  function flipAdd(card) {
    const currentCard = card;
    currentCard.classList.add('rotate');
    setTimeout(() => {
      currentCard.querySelector('.card-header').innerText = cards[id][index].translation;
      currentCard.querySelector('.card-header').classList.add('reflection');
    }, 150);
    currentCard.addEventListener('mouseleave', flipRemove);
  }

  if (e.target.classList.contains('rotate-button')) {
    flipAdd(e.target.parentNode.parentNode);
  } else {
    const src = `./assets/${cards[id][index].audioSrc}`;
    const audio = new Audio(src);
    audio.play();
    writeStats(id, index, 'clicks');
  }
}

function handlerAudioTrain() {
  const cardsItems = document.querySelectorAll('.card');


  cardsItems.forEach((card) => {
    if (window.play) {
      card.removeEventListener('click', hangsAnEvent);
    } else {
      card.addEventListener('click', hangsAnEvent);
    }
  });
}

export function activationLink(dataCategory) {
  [...document.querySelector('.category-menu').children].forEach((li) => {
    li.classList.remove('active');
    if (li.getAttribute('data-category') === dataCategory) {
      li.classList.add('active');
    }
  });
}

export function renderCategory(id) {
  document.querySelector('.btn-change-mode').disabled = false;
  deactivatedGame();
  activationLink(id);
  localStorage.setItem('page', 'category');
  localStorage.setItem('category', id);
  clearContainer(container);
  if (window.play) {
    const categoriesLayout = creatorCategoriesLayout(JSON.parse(localStorage.getItem('stats')), +id);
    container.innerHTML = categoriesLayout;
    document.querySelectorAll('.card').forEach((card) => {
      card.classList.add('text-white', 'bg-warning');
      card.children[1].classList.toggle('hidden');
    });
    handlerRotate();
    document.querySelector('.btn-play-container').classList.remove('hidden');
    document.querySelector('.btn-play').innerHTML = 'Star game';
  } else {
    const categoriesLayout = creatorCategoriesLayout(cards, +id);
    container.innerHTML = categoriesLayout;
    document.querySelectorAll('.card').forEach((card) => { card.classList.add('text-white', 'bg-info'); });
    handlerAudioTrain();
    handlerRotate();
    document.querySelector('.btn-play-container').classList.add('hidden');
    document.querySelector('.btn-play').innerHTML = 'Star game';
  }

  container.setAttribute('data-category', id);
}

function handlerMainLayoutClick() {
  const cardsItems = document.querySelectorAll('.card');
  cardsItems.forEach((card) => {
    card.addEventListener('click', (event) => {
      let { target } = event;
      while (target.classList.contains('card') !== true) {
        target = target.parentNode;
      }
      renderCategory(target.id);
    });
  });
}

export function renderMain() {
  deactivatedGame();
  activationLink('main');
  btnChange.disabled = false;
  localStorage.setItem('page', 'main');
  clearContainer(container);
  if (window.play) {
    container.innerHTML = mainLayout;
    handlerRotate();
    handlerMainLayoutClick();
    document.querySelectorAll('.card').forEach((card) => { card.classList.add('text-white', 'bg-warning'); });
  } else {
    container.innerHTML = mainLayout;
    handlerRotate();
    handlerMainLayoutClick();
    document.querySelectorAll('.card').forEach((card) => { card.classList.add('text-white', 'bg-info'); });
  }
}

renderMain();


function changeColorBtnChange() {
  btnChange.classList.toggle('btn-warning');
  btnChange.classList.toggle('btn-info');
  if (btnChange.innerHTML === 'play') btnChange.innerHTML = 'train';
  else btnChange.innerHTML = 'play';
}

function modeChange() {
  changeColorBtnChange();
  if (window.play) {
    window.play = 0;
    deactivatedGame();
  } else {
    window.play = 1;
  }
  handlerAudioTrain();
}
btnChange.addEventListener('click', () => {
  modeChange();
  [...document.querySelectorAll('.card')].forEach((card) => {
    card.classList.toggle('bg-info');
    card.classList.toggle('bg-warning');
    if (localStorage.getItem('page') === 'category') {
      card.children[1].classList.toggle('hidden');
    }
  });
  if (localStorage.getItem('page') === 'category') document.querySelector('.btn-play-container').classList.toggle('hidden');
});
