import { failPic, winPic } from './layout';
import { renderMain } from './render';
import { writeStats } from './statistics';


const container = document.querySelector('.row');
const starContainer = document.querySelector('.stars');
const btnChange = document.querySelector('.btn-change-mode');
let secredCardId;
let secredCardArray;
let indexCard = 0;

btnChange.addEventListener('click', () => {
  indexCard = 0;
});

export function clearContainer(divContainer) {
  while (divContainer.firstChild) {
    divContainer.removeChild(divContainer.firstChild);
  }
}

function shuffleCards(cards) {
  return cards.sort(() => Math.random() - 0.5);
}

function createrStars(result) {
  const star = document.createElement('span');
  const img = document.createElement('img');
  img.src = `assets/img/${result}.svg`;
  star.className = result;
  star.append(img);
  document.querySelector('.stars').prepend(star);
}

export function playSoundWord() {
  const categoryId = localStorage.getItem('category');
  const src = `./assets/${JSON.parse(localStorage.getItem('stats'))[categoryId][secredCardId].audioSrc}`;
  const audio = new Audio(src);
  audio.play();
}


function playSoundOfRightOrWrong(result) {
  const src = `./assets/audio/${result}.mp3`;
  const audio = new Audio(src);
  audio.play();
}


export function gameIteration(event) {
  const categoryId = container.getAttribute('data-category');
  let { target } = event;
  if (target.classList.contains('row')) return;
  while (target.classList.contains('card') !== true) {
    target = target.parentNode;
  }
  if (target.classList.contains('deactivated')) return;
  if (target.id === secredCardId) {
    target.classList.add('deactivated');
    writeStats(categoryId, target.id, 'winClicks');
    playSoundOfRightOrWrong('correct');
    createrStars('win');
    indexCard += 1;
    if (indexCard < 8) {
      secredCardId = secredCardArray[indexCard].id;
      setTimeout(playSoundWord, 1000);
    } else {
      endGame(document.querySelectorAll('.fail').length);
    }
  } else {
    writeStats(categoryId, target.id, 'failClicks');
    playSoundOfRightOrWrong('error');
    createrStars('fail');
  }
}

function selectCard(cardId) {
  secredCardId = cardId;
  setTimeout(playSoundWord, 1000);

  document.querySelector('.row').addEventListener('click', gameIteration);
}

function game() {
  secredCardArray = shuffleCards([...document.querySelectorAll('.card')]);
  selectCard(secredCardArray[indexCard].id);
}

export function startGame() {
  this.classList.add('active-btn-play');
  const repeat = '<img width=\'30px\' src="./assets/img/repeat2.svg">';
  this.innerHTML = repeat;
  this.removeEventListener('click', startGame);
  game();
  this.addEventListener('click', playSoundWord);
}
document.querySelector('.btn-play').addEventListener('click', startGame);


function endGame(fails) {
  btnChange.disabled = true;
  document.querySelector('.btn-play').addEventListener('click', startGame);
  indexCard = 0;
  clearContainer(container);
  clearContainer(starContainer);
  document.querySelector('.btn-play-container').classList.add('hidden');
  document.querySelector('.btn-play').classList.remove('active-btn-play');
  document.querySelector('.btn-play').removeEventListener('click', playSoundWord);
  document.querySelector('.row').removeEventListener('click', gameIteration);
  if (fails) {
    playSoundOfRightOrWrong('failure');
    container.innerHTML += `<div class="count-errors">${failPic}<div>${fails} error(s)</div></div>`;
    setTimeout(renderMain, 3000);
  } else {
    playSoundOfRightOrWrong('success');
    container.innerHTML = winPic;
    setTimeout(renderMain, 3000);
  }
}


export function deactivatedGame() {
  document.querySelector('.mode-container').classList.remove('hidden');
  clearContainer(document.querySelector('.stars'));
  document.querySelector('.btn-play').removeEventListener('click', playSoundWord);
  document.querySelector('.btn-play').innerHTML = 'Start game';
  document.querySelector('.btn-play').classList.remove('active-btn-play');
  document.querySelector('.row').removeEventListener('click', gameIteration);
  [...document.querySelectorAll('.deactivated')].forEach((card) => card.classList.remove('deactivated'));
  document.querySelector('.btn-play').addEventListener('click', startGame);
  indexCard = 0;
}
